/* src/ImageUpload.css */
.upload-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-image: url('bg_image.jpg'); /* Replace with your background image URL */
    background-size:120vw;
    background-position: right;
    scroll-behavior: smooth;
    color: white;
    position: relative;
    background-repeat:repeat-y;
}

.image-preview {
    margin-top: 20px;
    text-align: center;
}

.image-preview img {
    max-width: 300px;
    max-height: 300px;
}

.description-box {
    position: static;
    bottom: auto;
    left: 50%;
    /* transform: translate(-50%, 0); */
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    /* padding: 10px; */
    margin-top: 5vh;
    margin-left: 5vw;
    margin-right: 5vw;
    align-self: center;
    display: none;
    max-height: 150px;
    overflow: auto;
}

.description-box.visible {
    display: block;
}